<!--
  
  weiluxia 2016.08.19
  multiple 
  checkable  ok
  expandAll  ok
  expandedKeys  
  checkedKeys  ()
  selectedKeys 
  onExpand / ok
  onCheck  ok
  onSelect  ok
-->
<template>
  <ul class="v-tree" role="tree">
    <slot></slot>
  </ul>
</template>
<script>
  import './tree.scss';
  import vIcon from '../icon';
  import vCheckbox from '../checkbox';
  import * as Util from "../Util"; 
  export default {
    components: { vIcon, vCheckbox },
    props: {
      multiple: {
        type: Boolean,
        coerce: Util.coerceBoolean,
        default: true
      },
      checkable: {
        type: Boolean,
        coerce: Util.coerceBoolean,
        default: false
      },
      expandAll: {
        type: Boolean,
        coerce: Util.coerceBoolean,
        default: false
      },
      expandedKeys: null,
      checkedKeys: null,
      selectedKeys: null,
      onExpand: {
        type: Function,
        default () {
          return new Function();
        }
      },
      onCheck: {
        type: Function,
        default () {
          return new Function();
        }
      },
      onSelect: {
        type: Function,
        default () {
          return new Function();
        }
      }
    },
    data () {
      return {
        toolName: 'tree'
      }
    },
    methods: {
      // key
      getExpandKey () {
        let expandedKeys = this.expandedKeys;
        return expandedKeys;
      },
      setExpandKey () {
      },
      // key
      getCheckKey () {
        let expandedKeys = this.expandedKeys;
        return expandedKeys;
      },
      setCheckKey () {
      },
      // key
      getSelectKey () {
        let expandedKeys = this.expandedKeys;
        return expandedKeys;
      },
      setSelectKey () {
      },
      handleClickItem (key) {
        // 
        this.onSelect(key);
      },
      handleCheckItem (key) {
        // 
        this.onCheck(key);
      },
      handleExpandItem (key) {
        // 
        this.onExpand(key);
      },
    },
    ready () {
      const self = this;
      const expandedKeys = self.getExpandKey();
      const $children = self.$el.querySelectorAll('[role="tree-item"]');
      // $chidren(nodelist) 
      [...$children].forEach(($child, index) => {
        const vChild = $child.__vue__;
        const key = vChild.key;
        vChild.multiple = self.multiple;
        vChild.checkable = self.checkable;
        vChild.isExpanded = self.expandAll;
        let isExpanded = false;
        Array.isArray(expandedKeys) && (isExpanded = expandedKeys.includes(key));
        vChild.isExpanded = isExpanded;

        // 
        vChild.onItemClick = self.handleClickItem.bind(this, key);
        vChild.onItemCheck = self.handleCheckItem.bind(this, key);
        vChild.onItemExpand = self.handleExpandItem.bind(this, key);
      });
    }
  }
</script>


